@import url("https://fonts.googleapis.com/css2?family=Barlow+Semi+Condensed:wght@500;600&display=swap");
:root {
  --bg-violet: hsl(263, 55%, 52%);
  --bg-grayish: hsl(217, 19%, 35%);
  --bg-blackish: hsl(219, 29%, 14%);
  --bg-white: hsl(0, 0%, 100%);
  --main-profile: hsl(0, 0%, 81%);
  --sub-profile: hsl(0, 0%, 81%, 0.5);
  --main-profile-light: hsl(217, 19%, 35%);
  --sub-profile-light: hsl(217, 19%, 35%, 0.5);
  --head-text: hsl(210, 46%, 95%);
  --head-text-light: hsl(219, 29%, 14%);
  --content-text: hsl(210, 46%, 95%, 0.7);
  --content-text-light: hsl(219, 29%, 14%, 0.7);
}

body {
  font-family: "Barlow Semi Condensed", sans-serif;
}

:root {
  font-size: 13px;
}

*, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  display: grid;
  place-items: center;
  min-height: 100vh;
  background: var(--head-text);
}

.card-grid h1 {
  font-size: 1.5rem;
}
@media (min-width: 40em) {
  .card-grid h1 {
    font-size: 1.75rem;
  }
}
.card-grid p {
  line-height: 1.5625rem;
  font-size: 1rem;
}
@media (min-width: 40em) {
  .card-grid p {
    font-size: 1.125rem;
    line-height: 1.875rem;
  }
}
.card-grid h1.head-card {
  color: var(--head-text);
  margin: 1.5rem 0;
}
.card-grid p.content-card {
  color: var(--content-text);
}
.card-grid h1.head-card-light {
  color: var(--head-text-light);
}
.card-grid p.content-card-light {
  color: var(--content-text-light);
}
.card-grid span {
  margin-left: 1.5rem;
  display: block;
}
.card-grid span.main {
  color: var(--main-profile);
}
.card-grid span.sub {
  color: var(--sub-profile);
}
.card-grid span.main-light {
  color: var(--main-profile-light);
}
.card-grid span.sub-light {
  color: var(--sub-profile-light);
}

.container-grid {
  position: relative;
  max-width: 90%;
  display: grid;
  gap: 1.5rem;
  grid-template-areas: "violetGrid" "grayishGrid" "whiteSmoll" "blackishGrid" "whiteRow";
  grid-auto-columns: 1fr;
}
@media (min-width: 40em) {
  .container-grid {
    grid-template-areas: "violetGrid violetGrid whiteRow whiteRow" "blackishGrid blackishGrid grayishGrid grayishGrid" "whiteSmoll whiteSmoll whiteSmoll whiteSmoll";
  }
}
@media (min-width: 71.875em) {
  .container-grid {
    grid-template-areas: "violetGrid violetGrid grayishGrid whiteRow" "whiteSmoll blackishGrid blackishGrid whiteRow";
  }
}
.container-grid .violet-area {
  grid-area: violetGrid;
}
.container-grid .grayish-area {
  grid-area: grayishGrid;
}
.container-grid .blackish-area {
  grid-area: blackishGrid;
}
.container-grid .white-row-area {
  grid-area: whiteRow;
}
.container-grid .white-small-area {
  grid-area: whiteSmoll;
}

.card-grid {
  position: relative;
  width: 100%;
  max-width: 100%;
  margin: 0 auto;
  padding: 2em 3em;
  border-radius: 0.9375rem;
}

.profile {
  display: flex;
  align-items: center;
}
.profile img {
  border-radius: 50%;
  border: 1px solid #FFF;
  max-width: 2.5rem;
}

.card-grid-violet {
  background: var(--bg-violet);
}

.bg-pattern {
  background-image: url("../../../images/bg-pattern-quotation.svg");
  background-repeat: no-repeat;
  background-position: 95% 0;
}

.card-grid-grayish {
  background: var(--bg-grayish);
}

.card-grid-blackish {
  background: var(--bg-blackish);
}

.card-grid-white {
  background: var(--bg-white);
  box-shadow: 10px 35px 20px rgba(0, 0, 0, 0.1);
}